<template>
  <v-container fluid>
    <h1>Listado de productos</h1>
    <v-data-table
      :search="search"
      hide-default-footer
      :headers="headers"
      :items="products"
      sort-by="calories"
      class="elevation-1"
      @page-count="pageCount = $event"
    >
      <template v-slot:top>
        <v-toolbar flat color="white">
          <v-text-field
            v-model="search"
            append-icon="search"
            label="Buscar producto"
            single-line
            hide-details
          ></v-text-field>
          <v-divider class="mx-4" inset vertical></v-divider>
          <div class="flex-grow-1"></div>
          <v-dialog v-model="dialog" max-width="800px">
            <v-card>
              <v-card-title>
                <span class="headline">{{ formTitle }}</span>
              </v-card-title>
              <v-divider></v-divider>
              <v-container class="pa-5">
                <v-row>
                  <v-col cols="12" sm="12">
                    <p class="body-1 font-weight-bold">Modelo</p>
                    <v-text-field
                      outlined
                      v-model="editedItem.model"
                      placeholder="Nombre del modelo"
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6">
                    <p class="body-1 font-weight-bold">Tipo</p>
                    <v-select
                      v-model="editedItem.type"
                      :items="types"
                      item-text="name"
                      item-value="name"
                      outlined
                    ></v-select>
                  </v-col>
                  <v-col cols="12" sm="6">
                    <p class="body-1 font-weight-bold">Marca</p>
                    <v-select
                      v-model="editedItem.brand"
                      :items="brands"
                      item-text="name"
                      item-value="name"
                      outlined
                    ></v-select>
                  </v-col>
                  <v-col cols="12" sm="6">
                    <p class="body-1 font-weight-bold">Color</p>
                    <v-select v-model="editedItem.color" :items="colors" outlined></v-select>
                  </v-col>
                  <v-col cols="12" sm="6">
                    <p class="body-1 font-weight-bold">Precio (S/.)</p>
                    <v-text-field outlined v-model="editedItem.price" type="number"></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="12">
                    <p class="body-1 font-weight-bold">Descripci贸n</p>
                    <v-textarea
                      placeholder="Ingresa una descripci贸n"
                      outlined
                      v-model="editedItem.description"
                    ></v-textarea>
                  </v-col>
                </v-row>
              </v-container>
              <v-card-actions>
                <div class="flex-grow-1"></div>
                <v-btn outlined color="error" text @click="close">Cancelar</v-btn>
                <v-btn color="success" @click="save">Guardar</v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-toolbar>
      </template>
      <template v-slot:item.action="{ item }">
        <v-icon color="success" small class="mr-2" @click="editItem(item)">edit</v-icon>
        <v-icon color="error" small @click="deleteItem(item)">delete</v-icon>
      </template>
      <template v-slot:no-data>
        <v-alert type="error" :value="true">A煤n no cuentas con productos</v-alert>
      </template>
      <template v-slot:item.status="{item}">
        <v-chip v-if="item.status" color="success">Activo</v-chip>
        <v-chip v-else color="error">Inactivo</v-chip>
      </template>
    </v-data-table>
    <div class="text-center pt-2">
      <v-pagination v-model="page" :length="1"></v-pagination>
    </div>
  </v-container>
</template>

<script>
export default {
  data: () => ({
    search: "",
    dialog: false,
    page: 1,
    pageCount: 0,
    itemsPerPage: 10,
    headers: [
      { text: "Tipo", value: "type" },
      { text: "Marca", value: "brand" },
      { text: "Modelo", value: "model" },
      { text: "Color", value: "color" },
      { text: "Stock", value: "stock" },
      { text: "Precio", value: "price" },
      { text: "Acciones", value: "action", sortable: false }
    ],
    products: [],
    editedIndex: -1,
    editedItem: {
      type: "",
      brand: "",
      model: "",
      color: "",
      stock: 0,
      price: 0,
      description: ""
    },
    defaultItem: {
      type: "",
      brand: "",
      model: "",
      color: "",
      stock: 0,
      price: 0,
      description: ""
    }
  }),

  computed: {
    formTitle() {
      return "Editar producto";
    },
    brands() {
      return this.$store.getters.getBrands;
    },
    types() {
      return this.$store.getters.getTypes;
    },
    colors() {
      return this.$store.getters.getColors;
    }
  },

  watch: {
    dialog(val) {
      val || this.close();
    }
  },

  created() {
    this.initialize();
  },

  methods: {
    initialize() {
      this.products = this.$store.state.products;
    },

    editItem(item) {
      this.editedIndex = this.products.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    deleteItem(item) {
      const index = this.products.indexOf(item);
      confirm("驴Seguro que deseas eliminar este elemento?") &&
        this.products.splice(index, 1);
    },

    close() {
      this.dialog = false;
      setTimeout(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      }, 300);
    },

    save() {
      if (this.editedIndex > -1) {
        Object.assign(this.products[this.editedIndex], this.editedItem);
      } else {
        this.products.push(this.editedItem);
      }
      this.close();
    }
  }
};
</script>

